<template>
<!-- 单个项目的信息 -->
  <div class="project-info">
    <navBar :titleText="''"></navBar>
    <div class="head-info">
      <div class="item">
        <h3>{{infoObj.projectName}}</h3>
        <span 
        :style="infoObj.status == 'CHECKING_STATE' ? 'color: #00A3FF' : infoObj.status == 'EDITING_STATE' ? '#FF9B3F' : infoObj.status == 'DISAGREE_STATE' ? '#DE3124' : infoObj.status == 'FINISHED_STATE' ?  'color: #02CD53' : ''"
        >{{projectStateObj[infoObj.status]}}</span>
        <p>项目编号：{{infoObj.projectNum}}</p>
      </div>
    </div>
    <van-tabs v-model="active" color='#0247fe' title-active-color='#0247fe'>
      <van-tab title="项目信息" name="a">
        <van-cell center title="项目等级" :label="infoObj.projectLevel" />
        <van-cell center title="作业类型" :label="infoObj.jobType " />
        <van-cell center title="承包商名称" :label="infoObj.contractor" />
        <van-cell center title="项目经理" :label="infoObj.projectManager" />
        <van-cell center title="专业分类" :label="infoObj.professionalCategory" />
        <van-cell center title="项目进展" :label="infoObj.projectProgress" />
        <van-cell center title="计划开始时间" :label="infoObj.planStartTime" />
        <van-cell center title="计划结束时间" :label="infoObj.planCompletionTime" />
        <van-cell center title="实际开始时间" :label="infoObj.actualStartTime" />
        <van-cell center title="实际结束时间" :label="infoObj.actualCompletionTime" />
        <van-cell center title="监理单位" :label="infoObj.supervisionOrganization" />
        <van-cell center title="是否委托设计" :label="infoObj.commissionedDesign" />
        <van-cell center title="是否施工监理" :label="infoObj.constructionSupervision" />
        <van-cell center title="是否使用吊车" :label="infoObj.craneUse" />
        <van-cell center title="是否检修方案" :label="infoObj.repairPlan" />
        <van-cell center title="委托单位" :label="infoObj.delegateOrganization" />
        <van-cell center title="设计单位" :label="infoObj.designOrganization" />
        <van-cell center title="项目主管部门" :label="infoObj.projectAuthority" />
      </van-tab>
      <van-tab title="项目组织" name="b">
        <van-swipe-cell disabled>
          <template #left>
            <van-button square type="primary" text="选择" />
          </template>
          <div class="organize" @click="showPopup">
            <div><img src="https://fastly.jsdelivr.net/npm/@vant/assets/cat.jpeg"></div>
            <div><p>邓雄</p><span>质量经理</span></div>
          </div>
          <template #right>
            <van-button square type="danger" text="删除" />
            <van-button square type="primary" text="收藏" />
          </template>
        </van-swipe-cell>
        <van-swipe-cell>
          <template #left>
            <van-button square type="primary" text="选择" />
          </template>
          <div class="organize">
            <div><img src="https://fastly.jsdelivr.net/npm/@vant/assets/cat.jpeg"></div>
            <div><p>邓雄</p><span>质量经理</span></div>
          </div>
          <template #right>
            <van-button square type="danger" text="删除" />
            <van-button square type="primary" text="收藏" />
          </template>
        </van-swipe-cell>
        <!-- 人员详情弹窗 start -->
        <van-popup v-model="show" closeable class="person-details-popup" round style="width: 85%">
          <img src="https://fastly.jsdelivr.net/npm/@vant/assets/cat.jpeg">
          <p>邓雄</p>
          <span>项目经理</span>
          <div class="person-details">
            <div class="left">
              <span>身份证号</span>
              <span>年龄</span>
              <span>性别</span>
              <span>单位属性</span>
              <span>学历</span>
              <span>职称</span>
            </div>
            <div class="right">
              <span>111111111111111111</span>
              <span>33</span>
              <span>男</span>
              <span>&emsp;</span>
              <span>大专</span>
              <span>售后工程师</span>
            </div>
          </div>
        </van-popup>
        <!-- 人员详情弹窗 end -->
      </van-tab> 
      <van-tab title="项目方案" name="c">内容 3</van-tab>
      <van-tab title="物资供应" name="d">内容 4</van-tab>
    </van-tabs>
  </div>
</template>

<script>
import navBar from '@/components/navBar.vue'
export default {
  name: 'ProjectInfo',
  components: {
    navBar
  },
  data () {
    return {
      active: 'a',
      show: false,
      infoObj: {},
      projectStateObj: {} //  项目进度状态
    }
  },
  methods: {
    showPopup() {
      this.show = true
    },
    getState() {  //  获取项目进度状态的字典
      this.$getHttp(`${this.$baseUrl}/OS-PM/dict/getAll?enumClass=BaseData$ApprovalState`).then(res=> {
        // debugger
        let obj = {};
        if (res.status == 200) {
          res.data.map(item=> {
            for(let key in item) {
              obj[key] = item[key]
            }
          })
          this.projectStateObj = obj
        }
      }).catch(err=> {
        // debugger  
      })
    }
  },
  mounted() {
    // debugger
    this.infoObj = JSON.parse(this.$route.query.item);
  },
  created() {
    this.getState()
  }
}
</script>

<style lang='less' scoped>
  * {
    padding: 0;
    margin: 0;
  }
  .project-info {
    box-sizing: border-box;
    .head-info {
      .item {
        text-align: left;
         box-sizing: border-box;
         padding: 1rem;
         h3 {
          font-size: 0.875rem;
          font-weight: 400;
         }
        span {
          margin: 0.25rem 0rem;
          font-size: 0.75rem;
          // color: #00A3FF;
        }
        p {
          font-size: 0.75rem;
          color: #B4B4B4;
        }
      }
    }
    .van-tab__pane {
      padding: 1rem;
    }
    .van-cell {
      margin: 1rem 0rem;
    }
    .van-cell:first-child {
      margin-top: 0rem;
    }
    .van-cell__title {
      font-size: 1rem;
      .van-cell__label {
        font-size: 0.875rem;
        margin-top: 0.25rem;
      }
    }
    /deep/ .van-swipe-cell__left {  //  划卡的边框
      left: -1px;
    }
    /deep/ .van-swipe-cell__right {
      right: -1px;
    }
    .organize {
      height: 4.5rem;
      display: flex;
      box-sizing: border-box;
      padding: 0.75rem 0rem;
      div:nth-child(1) {
        width: 3rem;
        height: 3rem;
        margin-right: 1rem;
        img {
          width: 100%;
          height: 100%;
          border-radius: 50%;
        }
      }
      div:nth-child(2) {
        p {
          font-size: 1rem;
          color: #1E1E1E;
        }
        span {
          font-size: 0.875rem;
          color: #808187;
        }
      }
    }
    .person-details-popup {
      box-sizing: border-box;
      padding: 2rem 3rem;
      text-align: center;
      img {
        width: 6rem;
        height: 6rem;
        border-radius: 50%;
      }
      p {
        font-size: 1.375rem;
        font-weight: 500;
        margin-top: 1.5rem;
        margin-bottom: 0.5rem;
      }
      p+span {
        font-size: 1rem;
        color: #808187;
        margin-bottom: 1.5rem;
      }
      .person-details {
        display: flex;
        font-size: 0.875rem;
        color: #B4B4B4;
        div {
          display: flex;
          flex-direction: column;
          span {
            line-height: 1.25rem;
            margin: 0.25rem 0rem;
          }
        }
        .left {
          margin-right: 1rem;
          text-align: right;
        }
        .right {
          text-align: left;
        }
      }
    }
  }
</style>
